<template>
	<div class="infomationDetails">
		<div class="infomationDetails_header">
			<i class="icon_arrow_left_black" @click="goBack"></i>
		</div>
		<p class="infomationDetails_header_title">{{ dataObj.title }}</p>
		<p class="infomationDetails_header_source">{{ dataObj.source+ ' ' +dataObj.publish_time }}</p>
		<div class="infomationDetails_box" v-for="(item,index) in dataObj.paragraphs" :key="index">
			<img :src="item.paragraph_image? item.paragraph_image.img_url:''" alt="">
			<div v-html="item.paragraph">{{ item.paragraph }}</div>
		</div>
		<div class="redbag_box">
			<i class="icon_redbag_info" style="width: 1.64rem;height: 1.64rem;" id="icon_redbag"></i>
			<span>{{ $store.state.readCont+'/'+$store.state.redRewardCount }}</span>
		</div>
		<!-- <van-circle v-model="currentRate" :rate="30" :speed="100"  /> -->
		<canvas id="redbag" width="200" height="200"></canvas>
	</div>
</template>

<script>
	var timeR;
	let canvas;
	
	let ctx;
	export default {
		name:'infomationDetails',
		data(){
			return {
				item:{},
				innerHtml:[],
				dataObj:{},
				eAngle:0,
				currentRate:0,
				cont:0,
				scrollTop:0,
				redRewardAmount:0,
			}
		},
		computed: {
		    text() {
		      return this.currentRate.toFixed(0) + '%';
		    },
		  },
		mounted(){
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.item = _this.$route.query.item;
			_this.redRewardAmount = _this.$store.state.redRewardAmount;
			_this.getDetailsData();
			canvas=document.getElementById("redbag");
			var icon_redbag = document.getElementsByClassName('icon_redbag_info');
			var htmlFontSize = document.getElementsByTagName('html')[0].style.fontSize.replace('px','');
			canvas.width = icon_redbag[0].style.width.replace('rem','')*htmlFontSize +1;
			canvas.height = icon_redbag[0].style.height.replace('rem','')*htmlFontSize +1;
			ctx=canvas.getContext("2d");
			window.addEventListener('scroll', _this.handleScroll, true);
		},
		methods:{
			handleScroll(){
				let _this = this;
				var scrollTop = document.documentElement.scrollTop || document.body.scrollTop; //变量windowHeight是可视区的高度
				var windowHeight = document.documentElement.clientHeight || document.body.clientHeight; //变量scrollHeight是滚动条的总高度
				var scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight; //滚动条到底部的条件
				var proportion = 0;
				if(_this.scrollTop < scrollTop){
					_this.scrollTop = scrollTop;
					proportion = (Number(_this.scrollTop)+Number(windowHeight)) / Number(scrollHeight);
					_this.getCanvas(proportion);
					if(proportion == 1){
						_this.$store.commit('readArticle',1);
						_this.getDetailsData();
						if(_this.$store.state.readCont  == _this.$store.state.redRewardCount){
							_this.$toast({
								message:'恭喜获得奖励！',
								onClose:function(){
									_this.$store.commit('increment',_this.redRewardAmount);
									var ss = -_this.$store.state.readCont;
									_this.$store.commit('readArticle',ss);
									_this.infoReceiveReWard();
								}
							});
							
						}
					}
				}
			},
			infoReport(){
				let _this = this;
				var exclusiveObj = {
					browsingTime:25,
					contentId:_this.item.content_id,
					trackType:0,
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/info/report',
				  data:redata,
				}).then(function(res) {
					console.log(res);
				});
			},
			getDetailsData(){
				let _this = this;
				_this.$axios({
				  method: 'get',
				  url: _this.item.detail_url,
				}).then(function(res) {
					console.log(res);
					_this.dataObj = res.data;
					_this.dataObj.publish_time = new Date(_this.dataObj.publish_time*1000).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-');
				});
			},
			infoReceiveReWard(){
				let _this = this;
				var exclusiveObj = {
					contentId:_this.item.content_id
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/info/receiveReWard',
				  data:redata,
				}).then(function(res) {
					console.log(res);
				});
			},
			getCanvas(proportion){
				let _this = this;
					// 开始绘制路径
					ctx.lineWidth = 5;
					ctx.strokeStyle = '#ffff00';
					ctx.beginPath();
					// 绘制圆的路径**
					ctx.arc(canvas.width/2, canvas.height/2, (canvas.width-5)/2, 0, 6.28*proportion, false);
					// 描边路径
					ctx.stroke();
			},
			goBack(){
			  this.$router.back();
			},
		},
		beforeDestroy() {
			let _this = this;
			clearInterval(timeR);
			window.removeEventListener("scroll", _this.handleScroll, true);
		}
	}

</script>

<style scoped>
	.infomationDetails{
		width: 10.8rem;
		min-height: 100vh;
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-bottom:0.5rem;
		padding-left:0.44rem;
		padding-right:0.44rem;
	}
	.infomationDetails_header{
		width:100%;
		height: 1.51rem;
	}
	.icon_arrow_left_black{
		display: inline-block;
		vertical-align: middle;
		width: 0.56rem;
		height:0.56rem;
		background-image: url(../static/img/icon_arrow_left_black.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-top:0.47rem;
	}
	.infomationDetails_header_title{
		font-size: 0.72rem;
		color:#000;
		line-height: 0.98rem;
	}
	.infomationDetails_header_source{
		font-size: 0.36rem;
		color:#000;
		margin-top:0.32rem;
		line-height: 0.64rem;
		margin-bottom:0.4rem;
	}
	.infomationDetails_box{
		font-size: 0.48rem;
		line-height:0.78rem;
	}
	.infomationDetails_box img{
		width:100%;
		margin:0.1rem 0;
	}
	.redbag_box{
		width:2rem;
		height:2.4rem;
		background-color: #e2dff0;
		text-align: center;
		position: fixed;
		left:0;
		top:5.2rem;
		z-index: 4;
	}
	.redbag_box span{
		display: inline-block;
		vertical-align: middle;
		font-size: 0.36rem;
	}
	.icon_redbag_info{
		display: inline-block;
		vertical-align: middle;
		width:1.62rem;
		height:1.62rem;
		background-color: #716f78;
		background-image: url(../static/img/icon_redbag_info.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin:0.15rem 0;
		position: relative;
		z-index: 2;
		border-radius: 50%;
	}
	#redbag{
		position: fixed;
		left:0.19rem;
		top:5.35rem;
		z-index: 5;
		background: none;
		transform: rotateZ(-90deg);
	}
</style>
